@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100;0,9..40,400;0,9..40,500;0,9..40,700;0,9..40,900;1,9..40,400&display=swap");
:root {
  --flare: url(../images/flare.svg);
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  font-family: "DM Sans", sans-serif;
  background-color: #02020d;
  color: #e1e0fb;
}

h1,
h2,
h3,
h4 {
  text-align: center;
}

h1 {
  font-size: 3.2rem;
}

h2 {
  font-size: 2.8rem;
}

h3 {
  font-size: 2.4rem;
}

h4 {
  font-size: 2rem;
}

p {
  font-size: 1.6rem;
}

a {
  text-decoration: none;
}

.input {
  padding: 1rem;
  outline: none;
  border: none;
  border-radius: 0.5rem;
  margin-bottom: 2rem;
  background-color: #952b12;
  color: #e1e0fb;
  font-weight: 500;
  transition: background-color 150ms ease-in-out;
}
.input:focus {
  background-color: #b53416;
  transition: background-color 200ms ease-in-out;
}

.back-button {
  color: #ffffff;
  background-color: #8481f0;
  width: 100%;
  margin: 0;
  padding: 1rem;
  border: none;
  border-radius: 0.5rem;
  font-size: 2rem;
  font-weight: 500;
  text-align: center;
  transition: all 150ms;
  height: min-content;
  width: min-content;
  margin: 2rem;
}
.back-button:hover {
  transition: all 150ms;
  background-color: #b0aff5;
  cursor: pointer;
}
@media (max-width: 669px) {
  .back-button {
    margin: 2rem 0 0 2rem;
  }
}

