.no-margin {
  margin: 0;
}

.container {
  padding: 3rem;
}

/* USER-MENU (DROP-MENU) */
.user-menu {
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin: 0;
  padding: 0;
  background-color: none;
  background-blend-mode: overlay;
  border-radius: 1rem;
  position: absolute;
  top: 11rem;
  transition: all 150ms ease-in-out;
  opacity: 0;
  max-height: 0;
  overflow: hidden;
}
@media (max-width: 669px) {
  .user-menu {
    top: 26rem;
  }
}
.user-menu .user-menu__button--profile {
  color: #e1e0fb;
  background-color: #8481f0;
  width: 100%;
  margin: 0;
  padding: 0.5rem 0.8rem;
  border: none;
  border-radius: 0.5rem;
  font-size: 2rem;
  font-weight: 500;
  text-align: center;
  transition: all 150ms;
}
.user-menu .user-menu__button--profile:hover {
  transition: all 150ms;
  background-color: #b0aff5;
  cursor: pointer;
}
.user-menu .user-menu__button--settings {
  color: #e1e0fb;
  background-color: #952b12;
  width: 100%;
  margin: 0;
  padding: 0.5rem;
  border: none;
  border-radius: 0.5rem;
  font-size: 2rem;
  font-weight: 500;
  text-align: center;
  transition: all 150ms;
}
.user-menu .user-menu__button--settings:hover {
  transition: all 150ms;
  background-color: #c33817;
  cursor: pointer;
}
.user-menu .user-menu__button--logout {
  color: #952b12;
  background-color: #e7db37;
  width: 100%;
  margin: 0;
  padding: 0.5rem;
  border: none;
  border-radius: 0.5rem;
  font-size: 2rem;
  font-weight: 500;
  text-align: center;
  transition: all 150ms;
}
.user-menu .user-menu__button--logout:hover {
  transition: all 150ms;
  background-color: #ece365;
  cursor: pointer;
}
.user-menu .user-menu__button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  margin-top: 1rem;
  border-radius: 1rem;
  width: auto;
}
.user-menu .user-menu__button:first-of-type {
  margin: 0;
}
.user-menu.user-menu--showing {
  transition: all 300ms ease-in-out;
  padding: 1rem;
  max-height: 16rem;
  overflow: hidden;
  opacity: 1;
  z-index: 1;
  background-color: rgba(132, 129, 240, 0.5);
  backdrop-filter: blur(10px);
}

.landing-page {
  display: flex;
  flex-direction: column;
  height: 100vh;
}
.landing-page .landig-page__title {
  text-align: left;
  font-weight: 500;
  font-size: 4.8rem;
  margin: 5.4rem 0 0 5.4rem;
  height: 30%;
}
@media (max-width: 669px) {
  .landing-page .landig-page__title {
    height: auto;
    margin: 0;
  }
}
@media (max-width: 669px) {
  .landing-page {
    align-items: center;
    justify-content: center;
  }
}

.landing-page__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.landing-page__content .landing-page__paragraph {
  width: 50rem;
  font-size: 2.4rem;
}
@media (max-width: 669px) {
  .landing-page__content .landing-page__paragraph {
    width: auto;
    margin: 2rem 2rem;
    text-align: center;
  }
}
.landing-page__content .landing-page__link {
  text-transform: capitalize;
  position: relative;
  padding: 1rem;
  font-size: 2.4rem;
  color: #e7db37;
  border-radius: 1rem;
  font-weight: 500;
  background: rgb(250, 235, 239);
  background: linear-gradient(141deg, #e1e0fb 0%, #02020d 100%);
  background-size: 30rem;
  background-position: right;
  transition: all 300ms;
}
.landing-page__content .landing-page__link::before {
  content: var(--flare);
  position: absolute;
  bottom: 75%;
  right: 95%;
  transform: rotateZ(0deg);
  opacity: 0;
  transition: all 0.2s ease-in-out;
}
.landing-page__content .landing-page__link::after {
  content: "";
  position: absolute;
  bottom: 1rem;
  left: 1.2rem;
  width: 85%;
  height: 0.1em;
  opacity: 0;
  transition: all 300ms;
  background-color: #e7db37;
  transform: scale(0);
  border-radius: 1rem;
}
.landing-page__content .landing-page__link:hover {
  transition: all 300ms;
  background-position: center;
  -webkit-box-shadow: 0px 0px 24px -2px rgb(225, 224, 251);
  -moz-box-shadow: 0px 0px 24px -2px rgb(225, 224, 251);
  box-shadow: 0px 0px 24px -2px rgb(225, 224, 251);
}
.landing-page__content .landing-page__link:hover::before {
  content: var(--flare);
  position: absolute;
  bottom: 75%;
  right: 95%;
  transform: rotateZ(15deg);
  opacity: 1;
  transition: all 0.2s ease-in-out;
}
.landing-page__content .landing-page__link:hover::after {
  opacity: 1;
  transform: scale(1);
}

.login {
  height: 100vh;
  display: grid;
  place-items: center;
  grid-template-columns: 1fr 1fr;
  background: #02020d;
}
@media (max-width: 669px) {
  .login {
    display: flex;
    justify-content: center;
  }
}
.login .login__title {
  margin: 2rem;
  font-size: 3.2rem;
}
@media (max-width: 669px) {
  .login .login__title {
    display: none;
  }
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #e1e0fb;
  color: #02020d;
  padding: 3rem;
  font-size: 2rem;
  width: 100%;
  height: 100%;
}
@media (max-width: 669px) {
  .form {
    border-radius: 0.5rem;
    width: auto;
    height: auto;
  }
}
.form fieldset {
  display: flex;
  flex-direction: column;
  border: none;
  padding: 0;
}
.form label,
.form .input[type=submit] {
  font-weight: 500;
}
.form .form__submit {
  color: #e1e0fb;
  background-color: #952b12;
  width: 100%;
  margin: 0;
  padding: 1rem;
  border: none;
  border-radius: 0.5rem;
  font-size: 2rem;
  font-weight: 500;
  text-align: center;
  transition: all 150ms;
  width: 10rem;
}
.form .form__submit:hover {
  transition: all 150ms;
  background-color: #c33817;
  cursor: pointer;
}
@media (max-width: 669px) {
  .form .form__submit {
    width: 100%;
  }
}
.form .errors {
  color: #e7db37;
  font-weight: bold;
  text-align: justify;
  margin-bottom: 0.5rem;
}
.form .errors:last-of-type {
  margin-bottom: 2rem;
}
.form .errors::before {
  content: "⚠️";
}

.form__paragraph {
  margin: 1.6rem 0 0 0;
  text-align: center;
}
.form__paragraph .form__link {
  color: #02020d;
  font-weight: 500;
}

.conversations-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3rem 5.8rem 0 5.8rem;
  flex-direction: row;
  position: inherit;
}
.conversations-navigation .conversations-navigation__mesaje {
  font-size: 32px;
  width: auto;
}
@media (max-width: 669px) {
  .conversations-navigation {
    flex-direction: column;
    position: relative;
  }
  .conversations-navigation .conversations-navigation__mesaje {
    width: 17.4rem;
  }
}
.conversations-navigation .user {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 2rem;
}
.conversations-navigation .user .user__ppf {
  object-fit: cover;
  width: 7rem;
  height: 7rem;
  border-radius: 50%;
}

.conversations {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 5rem;
  margin: 3rem 5.8rem;
}
@media (max-width: 992px) {
  .conversations {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 669px) {
  .conversations {
    grid-template-columns: repeat(1, 1fr);
  }
}

.conversation {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #e1e0fb;
  color: #8481f0;
  padding: 1rem;
  border-radius: 0.5rem;
  height: 18rem;
  transition: all ease-in-out 200ms;
}
.conversation:hover {
  cursor: pointer;
  box-shadow: rgba(86, 97, 198, 0.4) 0px 5px, rgba(86, 97, 198, 0.3) 0px 10px, rgba(86, 97, 198, 0.2) 0px 15px, rgba(86, 97, 198, 0.1) 0px 20px, rgba(86, 97, 198, 0.05) 0px 25px;
}

.conversation__data {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.conversation__data .tags {
  display: flex;
  gap: 0.5rem;
  background-color: #952b12;
  color: #02020d;
  padding: 1rem;
  border-radius: 2rem;
  color: #e7db37;
  font-weight: 500;
}

.messages {
  display: grid;
  grid-template-columns: [break-start] 0.5fr [content-start] 2.5fr [content-end] 0.5fr [break-end];
  margin-bottom: 17rem;
}
@media (max-width: 669px) {
  .messages {
    grid-template-columns: [content-start] 1fr [content-end];
  }
}

.message {
  display: flex;
  justify-content: center;
  flex-direction: column;
  background-color: #ffffff;
  color: #141414;
  margin: 2rem 2rem 0 2rem;
  border-radius: 0.5rem;
  padding: 2rem;
  grid-area: content-start;
  grid-row: auto;
  position: relative;
}
.message .message__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: bold;
}
.message .message__info--date {
  text-align: center;
  width: fit-content;
}
@media (max-width: 992px) {
  .message .message__content {
    margin-bottom: 4rem;
  }
}

.user {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.user .user__ppf--message {
  width: 5rem;
  height: 5rem;
  object-fit: cover;
  border-radius: 50%;
}
.user .user__name {
  width: min-content;
  text-align: justify;
}
.user .user__name--you {
  background-color: #5a5ed8;
  padding: 0.2rem 0.2rem;
  border-radius: 0.5rem;
  height: min-content;
  color: #ffffff;
  text-align: center;
  font-weight: normal;
  padding: 0.3rem 1rem;
}

.message__actions {
  display: flex;
  gap: 1rem;
}
.message__actions img {
  width: 1.6rem;
}
.message__actions .message__actions--reply,
.message__actions .message__actions--delete,
.message__actions .message__actions--edit {
  display: flex;
  align-items: center;
  font-size: 1.6rem;
  gap: 1rem;
  transition: all 200ms;
  padding: 1rem;
  border-radius: 0.5rem;
}
.message__actions .message__actions--reply:hover,
.message__actions .message__actions--delete:hover,
.message__actions .message__actions--edit:hover {
  cursor: pointer;
  background-color: rgba(90, 94, 216, 0.3);
}
.message__actions .message__actions--reply {
  color: #5a5ed8;
}
.message__actions .message__actions--reply::after {
  content: "Reply";
}
.message__actions .message__actions--delete {
  color: #ff3838;
}
.message__actions .message__actions--delete::after {
  content: "Delete";
}
.message__actions .message__actions--edit {
  color: #5a5ed8;
}
.message__actions .message__actions--edit::after {
  content: "Edit";
}
@media (max-width: 992px) {
  .message__actions {
    position: absolute;
    bottom: 1.2rem;
    right: 1rem;
  }
}

.send {
  display: flex;
  justify-content: space-between;
  background-color: #141414;
  padding: 2rem;
  margin-top: 2rem;
  gap: 2rem;
  border-radius: 0.5rem 0.5rem 0 0;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 15rem;
}

.send__form {
  display: flex;
  flex-grow: 1;
  gap: 2rem;
}
.send__form .send__message {
  outline: none;
  border: none;
  background-color: #e2e2e2;
  height: 5rem;
  border-radius: 0.5rem;
  font-size: 1.6rem;
  font-weight: 500;
  width: 100%;
  height: 100%;
  vertical-align: top;
  padding: 1rem;
  resize: none;
}
.send__form .send__message:focus {
  outline: #5a5ed8 solid 2px;
}
.send__form .send__submit {
  outline: none;
  color: #e1e0fb;
  background-color: #8481f0;
  width: 100%;
  margin: 0;
  padding: 0.5rem 0.8rem;
  border: none;
  border-radius: 0.5rem;
  font-size: 2rem;
  font-weight: 500;
  text-align: center;
  transition: all 150ms;
  height: 5rem;
  width: min-content;
}
.send__form .send__submit:hover {
  transition: all 150ms;
  background-color: #b0aff5;
  cursor: pointer;
}